module.exports = "\n.drag-color1{\n  background-color: mediumvioletred;\n}\n.drag-color0{\n  background-color: white;\n}\n.column {\n\n  border: 3px solid gray; /* border around card */\n  background-color: #E2E4E6;\n  /* border-radius: 10px;  rounded corners of card */\n  width:  400px; /* !!! width of Column */\n  margin-left: 10px;\n  box-sizing: border-box;\n  display: inline-block;\n  vertical-align: top;\n  white-space: nowrap;\n  padding: 15px 15px 15px 15px;\n}\n.column h4 {\n  color: #4d4d4d;\n  line-height: 100%;\n  margin-left: 10px;\n  margin-top: 8px;\n  font-weight: 700;\n  line-height: 18px;\n  font-size: 14px;\n  margin-bottom: 3px;\n  height: 18px;\n  width: 260px;\n  display: inline-block;\n  cursor: pointer;\n  cursor: hand;\n}\n/* This outer which embeds inter is to make vertical alignement */\n.column-header-outer{\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 100%;\n  height:100px;\n  background-color: #4F5B6B;\n}\n.column-header {\n  width:100%;\n  color: #FFFFF9;\n  height: 20px;\n  min-height: 20px;\n  border: none;\n  line-height: 18px;\n  padding-left: 15px; /* this is left offset for column text */\n  font-size: 18px;\n  border-radius: 3px;\n  margin: 0px 0px 0px 0px;\n  /* background-color: #4F5B6B; column header background color */\n}\n.add-card {\n  color: #999;\n  font-size: 14px;\n  line-height: 18px;\n  margin-top: 0;\n  padding: 8px 10px;\n  cursor: pointer;\n  transition-property: all;\n  transition-duration: 150ms;\n  transition-timing-function: ease;\n}\n.add-card:hover {\n  text-decoration: underline;\n  color: #666;\n  background-color: #ccc;\n  border-bottom-left-radius: 3px;\n  border-bottom-right-radius: 3px;\n}\n.add-card input {\n  margin-left: -5px;\n  line-height: 18px;\n  font-size: 14px;\n  border: none;\n  border-radius: 3px;\n  padding: 5px 5px 4px 5px;\n  width: calc(100% - 10px);\n}\nul.card-list {\n  padding: 31px 11px 32px 9px;\n  margin: -26px 0 -31px 0;\n}\nul.card-list > li {\n  list-style: none;\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"